import React from 'react';
import '../Content/Style.css';
import State from './State';
import axios from 'axios';
import {Accordion, Card, Button} from 'react-bootstrap';
class India extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            Data : []
        }
    }
    componentDidMount(){

        axios.get("https://corona.lmao.ninja/v2/countries/india").then(response =>{
            
            this.setState({
               Data : response.data,
            })
        })
       }

render(){

    return(
        <div>
            <div className="container-fluid mb-5">
            <div className="row">
                <div className="col-md-12">
                <img className="LogoImg" src="https://www.countryflags.io/in/flat/64.png"></img>
            <h2 className="HeaderName">INDIA </h2>
                </div>
            </div>
            <div className="row mt-4">
              
                <div className="col-md-3">
                <Card className="CardStyle">
                   
                    <Card.Body className="Clrblue">
                        <Card.Title>Total Cases</Card.Title>
                        <Card.Text>
                        <h3>{this.state.Data.cases}</h3>
                        </Card.Text>
                      
                    </Card.Body>
                    </Card>
                </div>
                <div className="col-md-3">
                <Card className="CardStyle">
                    
                    <Card.Body className="Clrorange">
                        <Card.Title>Active Cases</Card.Title>
                        <Card.Text>
                        <h3>{this.state.Data.active}</h3>
                        </Card.Text>
                      
                    </Card.Body>
                    </Card>
                </div>
                <div className="col-md-3">
                <Card className="CardStyle">
                   
                    <Card.Body className="Clrgreen">
                        <Card.Title>Recoverd Cases</Card.Title>
                        <Card.Text>
                        <h3>{this.state.Data.recovered}</h3>
                        </Card.Text>
                      
                    </Card.Body>
                    </Card>
                </div>
                <div className="col-md-3">
                <Card className="CardStyle">
                   
                    <Card.Body className="Clrred">
                        <Card.Title>Death Cases</Card.Title>
                        <Card.Text>
                        <h3>{this.state.Data.deaths}</h3>
                        </Card.Text>
                      
                    </Card.Body>
                    </Card>
                </div>
           </div>
        <State />
        </div>
        </div>
    )

}
}

export default India;