import React from 'react';
import axios from 'axios';
import {Accordion, Card, Button} from 'react-bootstrap';

class State extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            stateData : {}
        }
    }

    componentDidMount(){

     axios.get("https://api.covid19india.org/state_district_wise.json").then(response =>{
         
         this.setState({
            stateData : response.data,
         })
     })
    }

render(){

    let keys = Object.keys(this.state.stateData);

    return(
        <div className="row mt-4">
        <div className="col-md-12">

                <Accordion defaultActiveKey="0">
                    {
                            keys.map((itm,ky)=>{
                                let districts = this.state.stateData[itm].districtData;
                              let district_keys = Object.keys(districts);
                                let total_Active = 0;
                                let total_Confirmed = 0;
                                let total_Death = 0;
                                let total_Recovery = 0;
                                let district_list = [];


                                for (let x in districts){
                                    total_Active +=  districts[x].active;
                                    total_Confirmed +=  districts[x].confirmed;
                                    total_Death +=  districts[x].deceased;
                                    total_Recovery +=  districts[x].recovered;
                                    let ob = districts[x];
                                    ob["district_name"] = x;
                                    district_list.push(ob);
                                }
                                
                                return(
                                    <Card>
                                    <Card.Header>
                                    <Accordion.Toggle as={Button} variant="link" eventKey={ky}>
                                        {itm} <span className="btn btn-primary ml-2">Active {total_Active}</span>
                                        <span className="btn btn-warning ml-2">Active {total_Confirmed}</span>
                                        <span className="btn btn-danger ml-2">Active {total_Death}</span>
                                        <span className="btn btn-success ml-2">Active {total_Recovery}</span>
                                    </Accordion.Toggle>
                                    </Card.Header>
                                    <Accordion.Collapse eventKey={ky}>
                                    <Card.Body>
                                        <table className="table table-bordered table-stripped">
                                            <thead>
                                                <tr>
                                                    <td>District</td>
                                                    <td>Confirmed</td>
                                                    <td>Active</td>
                                                    <td>Death</td>
                                                    <td>Recovered</td>
                                                </tr>
                                            </thead>
                                            <tbody>
                                               {

                                                   district_list.map((itm,ky)=>{
                                                       return(
                                                        <tr>
                                                        <td>{itm.district_name}</td>
                                                        <td>{itm.confirmed}</td>
                                                        <td>{itm.active}</td>
                                                        <td>{itm.deceased}</td>
                                                        <td>{itm.recovered}</td>
                                                    </tr>
                                                       )
                                                     
                                                   })
                                               }
                                            </tbody>
                                        </table>
                                    </Card.Body>
                                    </Accordion.Collapse>
                                </Card>
                                )
                            })

                    }
                   
               
                    </Accordion>
   </div>
</div>
    )

}
}

export default State;