import React from 'react';
import axios from 'axios';
class World extends React.Component{

    constructor(props){
        super(props);
   
    this.state = {
        worldData : []
    }
}
    componentDidMount(){

        axios.get("https://corona.lmao.ninja/v2/countries").then(response =>{
            
            this.setState({
               worldData : response.data,
            })
        })
       }

render(){

    return(
        <div className="container-fluid mb-5">
            <div className="row mt-4">
                <dov className="col-md-12">
                    <table className="table table-bordered table-stripped">
                    <thead>
                                                    <tr>
                                                        <td>Country</td>
                                                        <td>Total Cases</td>                                                     
                                                        <td>Death</td>
                                                        <td>Recovered</td>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                               {

                                                  this.state.worldData.map((itm,ky)=>{
                                                       return(
                                                        <tr>
                                                        <td>{itm.country} 
                                                        <img className="Imgcnflag" src={itm.countryInfo.flag}/>
                                                        </td>
                                                        <td>{itm.cases}</td>                                                        
                                                        <td>{itm.deaths}</td>
                                                        <td>{itm.recovered}</td>
                                                    </tr>
                                                       )
                                                     
                                                   })
                                               }
                                            </tbody>
                    </table>
                </dov>
            </div>

         </div>
    )

}
}

export default World;